* {
  box-sizing: border-box;
}

body {
    font-family: 'Open Sans', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
    font-size: 1em;
    font-weight: 300;
    line-height: 1.5;
    color: #777;
    background: #eee ;
    position: relative;
}

header {
  display: block;
  width: 100%;
  padding: 1.75em 0;
  border-bottom: 1px solid #eee;
  background: #fff;
}

header #logo {
  font-size: 175%;
  text-align: center;
  color: #1a8fff;
  line-height: 1;
}

header #logo .icon {
  padding-right: .25em;
}

main {
  display: block;
  margin: 0 auto;
  margin-top: 5em;
  max-width: 50em;
}


/* GRID */

.row {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.row-middle {
  align-items: center;
}

.col {
  flex-grow: 1;
  flex-basis: 0;
  max-width: 100%;
}

.col-start {
  justify-content: flex-start;
  text-align: left;
}

.col-center {
  justify-content: center;
  text-align: center;
}

.col-end {
  justify-content: flex-end;
  text-align: right;
}


/* Calendar */

.calendar {
  display: block;
  position: relative;
  width: 100%;
  background: #eee;
  border: 1px solid #fff;
}

.header {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 115%;
  padding: 1.5em 0;
  border-bottom: 1px solid #eee;
}

.icon {
  cursor: pointer;
  transition: .15s ease-out;
}

.icon:hover {
  transform: scale(1.75);
  transition: .25s ease-out;
  color: #1a8fff;
}

.icon:first-of-type {
  margin-left: 1em;
}

.icon:last-of-type {
  margin-right: 1em;
}

.days {
  text-transform: uppercase;
  font-weight: 400;
  color: darkgray;
  font-size: 70%;
  padding: .75em 0;
  border-bottom: 1px solid #eee;
}

.cell {
  position: relative;
  height: 3em;
  border-right: 1px solid #eee;
  overflow: hidden;
  cursor: pointer;
  background: #fff;
  transition: 0.25s ease-out;
}

.cell:hover {
  background: #f9f9f9;
  transition: 0.5s ease-out;
}

.selected {
  border-left: 10px solid transparent;
  border-image: linear-gradient(45deg, black 0%,black 40%);
  border-image-slice: 1;
}

.row {
  border-bottom: 1px solid #eee;
}

.row:last-child {
  border-bottom: none;
}

.cell:last-child {
  border-right: none;
}

.number {
  position: absolute;
  font-size: 82.5%;
  line-height: 1;
  top: .75em;
  right: .75em;
  font-weight: 700;
}

.disabled {
  color: #ccc;
  pointer-events: none;
}

.bg {
  font-weight: 700;
  line-height: 1;
  color: var(--main-color);
  opacity: 0;
  font-size: 8em;
  position: absolute;
  top: -.2em;
  right: -.05em;
  transition: .25s ease-out;}

